import ajax from '@utils/ajax';

// const serverPre = ajax.serverUrlMock;
const serverPre = ajax.serverUrl;
const serverName = '/MULTIDIMENSIONAL-MONITOR';
const serverNameUser = '/AUTH-USER-CENTER';

// 应用列表查询
export async function getApplicationList(page, size, name) {
  return ajax.get(
    serverPre(
      `${serverName}/tile-template/list?page=` +
        page +
        '&size=' +
        size +
        '&name=' +
        name
    )
  );
}
// 应用列表-新增模板
export async function applicationAdd(params) {
  return ajax.post(serverPre(`${serverName}/tile-template/add`), params);
}
// 应用列表-编辑模板
export async function applicationEdit(params) {
  return ajax.post(serverPre(`${serverName}/tile-template/edit`), params);
}
// 应用列表-模板详情
export async function applicationDetail(id) {
  return ajax.get(serverPre(`${serverName}/tile-template/detail?id=` + id));
}
// 应用列表-设置默认模板
export async function applicationDefault(id) {
  return ajax.get(serverPre(`${serverName}/tile-template/setDefault?id=` + id));
}
// 应用列表-用户同步
export async function applicationSync(params) {
  return ajax.post(
    serverPre(`${serverName}/tile-template/synchronization`),
    params
  );
}
// 应用列表-应用
export async function applicationApply(params) {
  return ajax.post(serverPre(`${serverName}/tile-template/apply`), params);
}
// 应用列表-删除模板
export async function applicationDelet(params) {
  return ajax.post(serverPre(`${serverName}/tile-template/del`), params);
}
// 同步,应用,模板用户列表,用于回显,根据部门deptCode查询用户
export async function getTemplateUsers(def, templateId) {
  return ajax.get(
    serverPre(
      `${serverName}/tile-template/templateUsers?def=` +
        def +
        '&templateId=' +
        templateId
    )
  );
}
// 查询部门列表,根据部门deptCode查询用户
export async function getDeptList() {
  return ajax.get(serverPre(`${serverNameUser}/dept/list`));
}
// 根据部门deptCode查询用户
export async function getUsersList(deptId) {
  return ajax.get(serverPre(`${serverNameUser}/user/dicts?deptId=` + deptId));
}
// 应用列表-磁贴数据
export async function applicationOperationList(params) {
  return ajax.get(serverPre(`${serverName}/operation/getData`), params);
}
// 应用列表-磁贴详情
export async function applicationOperationDetail(params) {
  return ajax.get(serverPre(`${serverName}/operation/detail`), params);
}

