<template>
  <div class="application-about-detail">
    <div class="application-about-detail-item right-item-wrap">
      <base-table-panel
        title="应用管理"
        class="sortTable noTableBorder"
        :data-source="applicationList"
        :right-btns="titleBtns()"
        :loading="applicationLoading"
        :columns="columns()"
        :show-summary="false"
        :total="applicationTotal"
        has-pagination
        :current-page="seachForm.page"
        :page-size="seachForm.limit"
        :border-style="true"
        :cell-class-name="applicationCellClassName"
        @handleCurrent="handleCurrentChange"
        @selection-change="selectionChange"
        @handleSize="handleSizeChange"
      >
        <template slot="operate-left">
          <el-form
            :inline="true"
            label-suffix=":"
            size="mini"
            @submit.native.prevent
            class="operate-left-form"
          >
            <el-form-item label="模板名称">
              <el-input v-model="seachForm.name"></el-input>
            </el-form-item>
            <!-- <el-form-item label="模板类型">
			  <el-select
			    clearable
			    placeholder="请选择模板类型"
			    filterable
			  >
			  </el-select>
			</el-form-item>
			<el-form-item label="模板状态">
			  <el-select
			    clearable
			    placeholder="请选择模板状态"
			    filterable
			  >
			  </el-select>
			</el-form-item> -->
            <el-form-item>
              <el-button type="primary" @click="handleSearch">查询</el-button>
            </el-form-item>
          </el-form>
        </template>
      </base-table-panel>
      <el-dialog
        :title="diolgTitle"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose"
      >
        <el-select
          v-model="deptCode"
          placeholder="请选择部门"
          @change="changeDept(deptCode)"
        >
          <el-option
            v-for="item in deptOptions"
            :key="item.deptCode"
            :label="item.deptName"
            :value="item.deptCode"
          >
          </el-option>
        </el-select>
        <el-tree
          :data="selectUserOptions"
          show-checkbox
          ref="tree"
          node-key="usercode"
          :props="defaultProps"
          :default-checked-keys="defaultChecked"
        >
        </el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="handleCheckChange()"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </div>
