    setSync(code) {
      applicationSync({ templateIds: this.selectionList, userCodes: code })
        .then(({ error }) => {
          this.btnLoading = false;
          if (!error) {
            this.$notify.success({
              title: '操作',
              message: '同步用户信息成功'
            });
            this._getApplicationList();
            this.dialogVisible = false;
          } else {
            this.$notify.error({
              title: '操作',
              message: error.message
            });
          }
        })
        .catch((err) => {
          this.$message.error('同步用户信息失败！');
          this.btnLoading = false;
        });
    },
    setApply(code) {
      applicationApply({ templateId: this.templateId, userCodes: code })
        .then(({ error }) => {
          this.btnLoading = false;
          if (!error) {
            this.$notify.success({
              title: '操作',
              message: '设置应用成功'
            });
            this._getApplicationList();
            this.dialogVisible = false;
          } else {
            this.$notify.error({
              title: '操作',
              message: error.message
            });
          }
        })
        .catch((err) => {
          this.$message.error('设置应用失败！');
          this.btnLoading = false;
        });
    },
    // 选择用户列表,设置应用或设置同步
    handleCheckChange() {
      console.log(this.diolgType);
      const userCode = this.$refs.tree
        .getCheckedKeys()
        .concat(this.$refs.tree.getHalfCheckedKeys());
      if (this.diolgType === '设置应用') {
        this.setApply(userCode);
      }
      if (this.diolgType === '批量同步' || this.diolgType === '数据同步') {
        this.setSync(userCode);
      }
    },
    // 查询同步,应用模板用户,已应用的用户信息用户回显
    _getTemplateUsers(def, id) {
      const arrayId = [];
      arrayId.push(id);
      getTemplateUsers({ def: def, templateId: arrayId }).then((res) => {
        this.defaultChecked = res.data;
      });
    },
    handleSizeChange() {
      this.seachForm.page = 1;
      this.seachForm.limit = 10;
      this._getApplicationList();
    },
    handleCurrentChange(val) {
      this.seachForm.page = val;
      this._getApplicationList();
    },
    //跳转应用管理新增页面
    showApplicationAdd() {
      this.$router.push({ name: 'applicationAdd', params: { type: 'add' } });
    },
    // 模板编辑
    getApplicationEdit(row) {
      this.$router.push({
        name: 'applicationAdd',
        params: { type: 'edit', id: row.id }
      });
    },
    // 设置默认
    _applicationDefault(row) {
      if (row.def === 0) {
        this.$confirm('确定要设置默认吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.btnLoading = true;
          applicationDefault(row.id)
            .then(({ error }) => {
              this.btnLoading = false;
              if (!error) {
                this.$notify.success({
                  title: '操作',
                  message: '设置默认成功'
                });
                this._getApplicationList();
              } else {
                this.$notify.error({
                  title: '操作',
                  message: error.message
                });
              }
            })
            .catch((err) => {
              this.$message.error('设置默认失败！');
              this.btnLoading = false;
            });
        });
      } else {
        this.$confirm('确定要取消默认吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.btnLoading = true;
          applicationDefault(row.id)
            .then(({ error }) => {
              this.btnLoading = false;
              if (!error) {
                this.$notify.success({
                  title: '操作',
                  message: '取消默认成功'
                });
                this._getApplicationList();
              } else {
                this.$notify.error({
                  title: '操作',
                  message: error.message
                });
              }
            })
            .catch((err) => {
              this.$message.error('取消默认失败！');
              this.btnLoading = false;
            });
        });
      }
    },
    // 设置应用
    _applicationApply(row) {
      this.dialogVisible = true;
      this.diolgTitle = '设置应用';
      this.diolgType = '设置应用';
      this._getTemplateUsers(1, row.id);
      this.templateId = row.id;
    },
    delList(params) {
      applicationDelet(params)
        .then(({ error }) => {
          this.btnLoading = false;
          if (!error) {
            this.$notify.success({
              title: '操作',
              message: '删除成功'
            });
            this._getApplicationList();
          } else {
            this.$notify.error({
              title: '操作',
              message: error.message
            });
          }
        })
        .catch((err) => {
          this.$message.error('删除失败！');
          this.btnLoading = false;
        });
    },
    // 删除
    _applicationDelet(row) {
      const arrayId = [];
      arrayId.push(row.id);
      this.$confirm('确定要删除当前模板吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.btnLoading = true;

        const params = {
          templateIds: arrayId
        };
        this.delList(params);
      });
    }
  }
};
